<template>
  <div class="home">
    <!-- <HomeHeader :city="city"></HomeHeader>store -->
    <HomeHeader ></HomeHeader>
    <HomeSwiper :List="swiperList"></HomeSwiper>
    <HomeIcons :list="iconList"></HomeIcons>
    <HomeRecommend :List="recommendList"></HomeRecommend>
    <HomeWeekend :List="weekendList"></HomeWeekend>
   
  </div>
</template>

<script>
// @ is an alias to /src

import HomeHeader from "./zujian/Header.vue";
import HomeSwiper from "./zujian/swiper.vue";
import HomeIcons from "./zujian/Icons.vue";
import HomeRecommend from "./zujian/Recommend.vue";
import HomeWeekend from "./zujian/weekend.vue";
import axios from "axios";
import { mapState } from 'vuex' ;
export default {
  name: "home",
  components: {
    HomeHeader,
    HomeSwiper,
    HomeIcons,
    HomeRecommend,
    HomeWeekend
  },
  data () {
    return {
      // city :"",ajax
      lastCity : '' ,
      swiperList : [],
      iconList : [],
      recommendList : [],
      weekendList : [],
    }
    // swiper mounted 
  },
  // ajax url then200
  computed : {
    ...mapState (['city'])
  },
  methods : {
    getHomeinfo () {
      axios.get('indexs.json?city=' + this.city)
      .then(this.getHomeinfosucc)
    },
    getHomeinfosucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        // this.city = data.city
        this.swiperList = data.swiperList
        this.iconList = data.iconList
        this.recommendList = data.recommendList
        this.weekendList = data.weekendList
      }
      console.log(res)
    }
  },
  // ajax 
  mounted () {
    this.lastCity = this.city
    this.getHomeinfo()
  },
  activated () {
    if (this.lastCity !== this.city) {
      this.lastCity = this.city
      this.getHomeinfo()
    }
    //  mountedkeep-live
  }
};
</script>
